 import React, { useState, useEffect } from "react";
import "./home_style.css";
import { BrowserRouter as Router, Route, Switch, Link, Routes, useNavigate } from 'react-router-dom';

import Card from "../elements/card";
import TripItineraryPage from "../pages/trip_itinerary";
import Searchbar from "../elements/searchbar";
import Modal from "../elements/modal";
import Navbar from "../elements/navbar";
import Footer from "../elements/footer";

function Home() {
  const [trips_list, setTrip_list] = useState([]);
  const [loading, setLoading] = useState(true);

  const [isOpen, setIsOpen] = useState(false);
  const history = useNavigate();

  const [location , setLocation] = useState('');

  useEffect(() => {
    setIsOpen(true); // Set isOpen to true when component mounts
    // fetch("http://157.245.96.141:5000/all_trips")
   fetch("http://localhost:5000/all_trips")
      .then((response) => response.json())
      .then((data) => {
        setTrip_list(data);
        setLoading(false);
      })
      .catch((error) => {
        console.error("Error fetching data:", error);
        setLoading(false);
      });
    console.log("trips", trips_list);
  }, []);

  const handleClose = () => {
    setIsOpen(false);
  };

  return (
    <div>
      <Modal isOpen={isOpen} onClose={handleClose} location={location} />

      {/* Search Bar */}
      <div>
      <Searchbar />
      </div>

      {/* Card Grid */}
      <h2 className="Trips-header-text">Upcomming Trending Trips</h2>
      <div className="home-card-grid">
        {loading ? (
          <div>Loading...</div>
        ) : (
          trips_list.map((i) => {
            return (
                  <div onClick={()=>{history(`/${i.host_name}/${i.trip_name}`)}}>
                  <Card
                    trip_name={i.trip_name}
                    price={i.price}
                    boarding={i.boarding}
                    destination={i.destination}
                  />
              </div>
            );
          })
        )}

      </div>
    </div>
  );
}

export default Home;
