import React, { useState } from 'react';
import './modal_style.css'; // Example CSS for styling the modal

const Modal = ({ isOpen, onClose ,location }) => {
  if (!isOpen) return null;
  
  return (
    <div className="modal-overlay">
      <div className="modal">
        <h2>Welcome to Peak2Beach</h2>
        <p>Please enter your for better search results</p>
        {/* <input placeholder='Enter your Location...' value={location}  onChange={(e)=>{setLocation(e)}}/> */}
        <button onClick={onClose}>submit</button>
      </div>
    </div>
  );
};

export default Modal;