import React, { useState ,useEffect } from 'react';
import './trip_itinerary_style.css'; 
import { useParams } from 'react-router-dom';

function TripItineraryPage() {

  const trip_name = useParams().trip_name;

  const [name, setName] = useState('');
  const [email, setEmail] = useState('');
  const [phone, setPhone] = useState('');
  const [date, setDate] = useState('');
  const [count, setCount] = useState('');
  const [message, setMessage] = useState('');

  const handleSubmit = (event) => {
    event.preventDefault();
    console.log('Name:', name);
    console.log('Email:', email);
    console.log('Phone:', phone);
    console.log('Date:', date);
    console.log('Count:', count);
    console.log('Message:', message);
  };
  const images = [
    'https://source.unsplash.com/random/800x600?nature',
    'https://source.unsplash.com/random/800x600?water',
    'https://source.unsplash.com/random/800x600?mountains',
    'https://source.unsplash.com/random/800x600?beach',
  ];

  const [currentImage, setCurrentImage] = useState(0);

  useEffect(() => {
    const interval = setInterval(() => {
      setCurrentImage((currentImage + 1) % images.length);
    }, 5000);

    return () => clearInterval(interval);
  }, [currentImage, images.length]);




  return (
    <div className="container">
      <div className="left-container">
        <div className="tour-card">
          <h2>{trip_name}</h2>
          <div className="rating">
            <span>4.5/5</span>
            <span>(34 Reviews)</span>
          </div>
          <div className="details">
            <span>4D/3N</span>
            <span>Panjim</span>
          </div>
          <div className="icons">
            <div className="icon">
              <img src="transport-icon.svg" alt="Transport Included" />
              <p>Transport Included</p>
            </div>
            <div className="icon">
              <img src="meal-icon.svg" alt="Meals Included" />
              <p>Meals Included</p>
            </div>
            <div className="icon">
              <img src="sightseeing-icon.svg" alt="Sightseeing" />
              <p>Sightseeing</p>
            </div>
            <div className="icon">
              <img src="stay-icon.svg" alt="Stay Included" />
              <p>Stay Included</p>
            </div>
          </div>
          <h3>{trip_name}</h3>
          <ul>
            <li>Relish a perfect getaway to the land that offers the beauty of shimmering beaches, stellar
            nightlife, thrill of adventure activities and so much more</li>
            <li>Witness the architectural brilliance of Basilica of Bom Jesus, a UNESCO world heritage site that
            holds the mortal remains of St. Francis Xavier</li>
            <li>Walk barefoot on the soft sands under the clear sky and feel the mellow waves kissing your feet</li>
            <li>Treat your taste buds with delicious Goan cuisine and exotic Sea Food that also has a hint of
            Portuguese cuisine</li>
            <li>Marvel at the Goan shore line from the lighthouse of the famous Aguada Fort that proudly stands
            tall on the shores and offers panoramic views of the ocean</li>
          </ul>
          <h3>Backpacking Tour to Goa Overview</h3>
        </div>
      </div>
      <div className="right-container">
        <div className="price-card">
          <p>Starting from INR 10,000</p>
          <h2>INR 7,299 <span>per Adult</span></h2>
          <span>27% Off</span>
        </div>
        <form onSubmit={handleSubmit}>
          <h3>Backpacking Tour to Goa: Get 27% off!</h3>
          <div className="form-group">
            <label htmlFor="name">Your Name</label>
            <input
              type="text"
              id="name"
              value={name}
              onChange={(e) => setName(e.target.value)}
            />
          </div>
          <div className="form-group">
            <label htmlFor="email">Your Email</label>
            <input
              type="email"
              id="email"
              value={email}
              onChange={(e) => setEmail(e.target.value)}
            />
          </div>
          <div className="form-group">
            <label htmlFor="phone">Phone</label>
            <input
              type="tel"
              id="phone"
              value={phone}
              onChange={(e) => setPhone(e.target.value)}
            />
          </div>
          <div className="form-group">
            <label htmlFor="date">Choose Date of Travel</label>
            <input
              type="date"
              id="date"
              value={date}
              onChange={(e) => setDate(e.target.value)}
            />
          </div>
          <div className="form-group">
            <label htmlFor="count">Traveller Count</label>
            <input
              type="number"
              id="count"
              value={count}
              onChange={(e) => setCount(e.target.value)}
            />
          </div>
          <div className="form-group">
            <label htmlFor="message">Message</label>
            <textarea
              id="message"
              value={message}
              onChange={(e) => setMessage(e.target.value)}
            />
          </div>
          <button type="submit">Book Now</button>
          <div className="privacy-notice">
            <ul>
              <li> We assure the privacy of your contact data.</li>
              <li> This data will only be used by our team to contact you and no other purposes.</li>
            </ul>
          </div>
        </form>
      </div>
    </div>
  );
}

export default TripItineraryPage;

const Carousel = ({ images }) => {
  const [currentImage, setCurrentImage] = useState(0);

  useEffect(() => {
    const interval = setInterval(() => {
      setCurrentImage((currentImage + 1) % images.length);
    }, 5000);

    return () => clearInterval(interval);
  }, [currentImage, images.length]);

  return (
    <div className="carousel">
      <div className="carousel-inner">
        {images.map((image, index) => (
          <div
            key={index}
            className={`carousel-item ${index === currentImage ? 'active' : ''}`}
          >
            <img src={image} alt="" />
          </div>
        ))}
      </div>
    </div>
  );
};

