import React, { useState } from "react";
import ImageCarousel from "../elements/iter_image_carousel.jsx";
import { useParams } from "react-router-dom";
import ImageGrid from "../elements/image_grid.jsx";
import RichTextEditor from "../elements/TextEditor.jsx";
import './host_form_style.css';
import '../icons/transport.svg'

function Host_form() {
  const [trip_name, settrip_name] = useState("");
  const [name, setName] = useState("");
  const [email, setEmail] = useState("");
  const [phone, setPhone] = useState("");
  const [websitelink, setWebsitelink] = useState("");
  const [social, setSocial] = useState("");
  const [message, setMessage] = useState("");

  const handleSubmit = (event) => {
    event.preventDefault();
    console.log("Name:", name);
    console.log("Email:", email);
    console.log("Phone:", phone);
    console.log("Websitelink:", websitelink);
    console.log("Social:", social);
    console.log("Message:", message);
  };

  const [checkedItems, setCheckedItems] = useState({
    Transport: false,
    Stay: false,
    Selected_Meals: false,
    All_Meals: false,
    First_aid_kit: false,
    Camping:false,
    Adventure_activities:false,
    Trekking:false,
    Team_captain:false,
    SightSeeing:false,
    oxigen_cylinder:false,
  });

  const handleCheckboxChange = (event) => {
    setCheckedItems({
      ...checkedItems,
      [event.target.name]: event.target.checked,
    });
  };

  const [boardingPoints, setBoardingPoints] = useState([]);

  const addBoardingPoint = () => {
    setBoardingPoints([...boardingPoints, { date: "", boardingPoint: "" }]);
  };

  const handleDateChange = (index, date) => {
    const updatedBoardingPoints = [...boardingPoints];
    updatedBoardingPoints[index].date = date;
    setBoardingPoints(updatedBoardingPoints);
  };

  const handleBoardingPointChange = (index, boardingPoint) => {
    const updatedBoardingPoints = [...boardingPoints];
    updatedBoardingPoints[index].boardingPoint = boardingPoint;
    setBoardingPoints(updatedBoardingPoints);
  };

  const deleteBoardingPoint = (index) => {
    const updatedBoardingPoints = [...boardingPoints];
    updatedBoardingPoints.splice(index, 1);
    setBoardingPoints(updatedBoardingPoints);
  };

  const [dropingPoints, setDropingPoints] = useState([]);

  const addDropingPoint = () => {
    setDropingPoints([...dropingPoints, { date: "", dropingPoints: "" }]);
  };

  const handleDropingDateChange = (index, date) => {
    const updatedDropingPoints = [...dropingPoints];
    updatedDropingPoints[index].date = date;
    setDropingPoints(updatedDropingPoints);
  };

  const handleDropingPointChange = (index, dropingPoint) => {
    const updatedDropingPoints = [...dropingPoints];
    updatedDropingPoints[index].dropingPoint = dropingPoint;
    setDropingPoints(updatedDropingPoints);
  };

  const deleteDropingPoint = (index) => {
    const updatedDropingPoints = [...dropingPoints];
    updatedDropingPoints.splice(index, 1);
    setDropingPoints(updatedDropingPoints);
  };

  const [destinations, setDestination] = useState([]);

  const addDestination = () => {
    setDestination([...destinations, { date: "", destinations: "" }]);
  };

  const handleDestinationChange = (index, destination) => {
    const updatedDestination = [...destinations];
    updatedDestination[index].destination = destination;
    setDestination(updatedDestination);
  };

  const deleteDestination = (index) => {
    const updatedDestination = [...destinations];
    updatedDestination.splice(index, 1);
    setDestination(updatedDestination);
  };

  return (
    <div className="app">                  
      <ImageCarousel />
      {/* <ImageGrid /> */}
      <div className="container">
        <div className="left-container">
          {/* <div className="tour-card"> */}
          <input
            placeholder="Trip Name"
            value={trip_name}
            onChange={(value) => {
              settrip_name(value.target.value);
            }}
          />
          <h1>{trip_name}</h1>
          <div className="details">
          </div>
          <div className="icons">
            <div className="icon">
              <div className="list-container">
                <h3>Boardings</h3>
                <div className="boarding-points">
                  <div className="header">
                    {/* <span>Boarding Point</span>
                      <span>dd / mm / yyyy </span> */}
                    {/* <span className="calendar-icon"></span> */}
                  </div>
                  {boardingPoints.map((boardingPoint, index) => (
                    <div key={index} className="boarding-point">
                      <input
                        type="text"
                        placeholder="add boarding"
                        value={boardingPoint.boardingPoint}
                        onChange={(e) =>
                          handleBoardingPointChange(index, e.target.value)
                        }
                      />
                      <input
                        type="date"
                        value={boardingPoint.date}
                        onChange={(e) =>
                          handleDateChange(index, e.target.value)
                        }
                      />
                      <button
                        className="delete-button"
                        onClick={() => deleteBoardingPoint(index)}
                      >
                        Del
                      </button>
                    </div>
                  ))}
                </div>
                <button className="add-button" onClick={addBoardingPoint}>
                  add boardings
                </button>
              </div>
            </div>
            {/* <div className="icon">
                <img src="meal-icon.svg" alt="Meals Included" />
                <p>Meals Included</p>
              </div> */}
            <div className="icon">
              <div className="list-container">
                <h3>Droping Points</h3>
                <div className="droping-points">
                  <div className="header">
                    {/* <span>Boarding Point</span>
                      <span>dd / mm / yyyy </span> */}
                    {/* <span className="calendar-icon"></span> */}
                  </div>
                  {dropingPoints.map((dropingPoint, index) => (
                    <div key={index} className="droping-point">
                      <input
                        type="text"
                        placeholder="add droping point"
                        value={dropingPoint.dropingPoint}
                        onChange={(e) =>
                          handleDropingPointChange(index, e.target.value)
                        }
                      />
                      <input
                        type="date"
                        value={dropingPoints.date}
                        onChange={(e) =>
                          handleDropingDateChange(index, e.target.value)
                        }
                      />
                      <button
                        className="delete-button"
                        onClick={() => deleteDropingPoint(index)}
                      >
                        Del
                      </button>
                    </div>
                  ))}
                </div>
                <button className="add-button" onClick={addDropingPoint}>
                  add Dropings
                </button>
              </div>
            </div>
            <div className="icon">
              <div className="list-container">
                <h3>Destinations</h3>
                <div className="destination-points">
                  <div className="header">
                    {/* <span>Boarding Point</span>
                      <span>dd / mm / yyyy </span> */}
                    {/* <span className="calendar-icon"></span> */}
                  </div>
                  {destinations.map((destination, index) => (
                    <div key={index} className="destination-point">
                      <input
                        type="text"
                        placeholder="add Destination"
                        value={destination.destination}
                        onChange={(e) =>
                          handleDestinationChange(index, e.target.value)
                        }
                      />
                      <button
                        className="delete-button"
                        onClick={() => deleteDestination(index)}
                      >
                        Del
                      </button>
                    </div>
                  ))}
                </div>
                <button className="add-button" onClick={addDestination}>
                  add Destination
                </button>
              </div>
            </div>
          </div>
          <h2>Inclusions </h2>
          <div className="checkbox-list">
            <label className="checkbox-item">
              <input
                type="checkbox"
                name="Transport"
                checked={checkedItems.Transport}
                onChange={handleCheckboxChange}
              />
              Transport
            </label>
            <label className="checkbox-item">
              <input
                type="checkbox"
                name="Stay"
                checked={checkedItems.Stay}
                onChange={handleCheckboxChange}
              />
              Stay
            </label>
            <label className="checkbox-item">
              <input
                type="checkbox"
                name="Selected_Meals"
                checked={checkedItems.Selected_Meals}
                onChange={handleCheckboxChange}
              />
              Selected meals
            </label>
            <label className="checkbox-item">
              <input
                type="checkbox"
                name="All_Meals"
                checked={checkedItems.All_Meals}
                onChange={handleCheckboxChange}
              />
              All Meals
            </label>
            <label className="checkbox-item">
              <input
                type="checkbox"
                name="First_aid_kit"
                checked={checkedItems.First_aid_kit}
                onChange={handleCheckboxChange}
              />
              First Aid Kit
            </label>
            <label className="checkbox-item">
              <input
                type="checkbox"
                name="Camping"
                checked={checkedItems.Camping}
                onChange={handleCheckboxChange}
              />
              Camping
            </label>
            <label className="checkbox-item">
              <input
                type="checkbox"
                name="Adventure_activities"
                checked={checkedItems.Adventure_activities}
                onChange={handleCheckboxChange}
              />
              Adventure_activities
            </label>
            <label className="checkbox-item">
              <input
                type="checkbox"
                name="Trekking"
                checked={checkedItems.Trekking}
                onChange={handleCheckboxChange}
              />
              Trekking
            </label>
            <label className="checkbox-item">
              <input
                type="checkbox"
                name="Team_captain"
                checked={checkedItems.Team_captain}
                onChange={handleCheckboxChange}
              />
              Team_captain/Guide
            </label>
            <label className="checkbox-item">
              <input
                type="checkbox"
                name="SightSeeing"
                checked={checkedItems.SightSeeing}
                onChange={handleCheckboxChange}
              />
              SightSeeing
            </label>
            <label className="checkbox-item">
              <input
                type="checkbox"
                name="oxigen_cylinder"
                checked={checkedItems.oxigen_cylinder}
                onChange={handleCheckboxChange}
              />
              oxigen_cylinder
            </label>
          </div>
          <div className="Inclusion-container">
            {
                checkedItems.Transport?<div>
                    {/* C:\Users\shrey\peak2beach\src\icons\transport.svg */}
                    <img src="../icons/transport.svg" className="icon-image" alt="transport-Icon"/>
                    <p><strong>Transport</strong></p>
                    
                    </div>:<></>
            }
            {
                checkedItems.Stay?<div>
                  <img src="../icons/stay.svg" className="icon-image" alt="Stay-Icon" />
                  
                  </div>:<></>
            }
            {
                checkedItems.Selected_Meals?<div><h1>Selected_Meals</h1></div>:<></>
            }
            {
                checkedItems.All_Meals?<div><h1>All_Meals</h1></div>:<></>
            }
            {
                checkedItems.First_aid_kit?<div><h1>First-Aid kit</h1></div>:<></>
            }
            {
                checkedItems.Camping?<div><h1>Camping</h1></div>:<></>
            }
            {
                checkedItems.Adventure_activities?<div><h1>Adventure Activities</h1></div>:<></>
            }
            {
                checkedItems.Trekking?<div><h1>Trekking</h1></div>:<></>
            }
            {
                checkedItems.Team_captain?<div><h1>Team_captain</h1></div>:<></>
            }
            {
                checkedItems.SightSeeing?<div><h1>SightSeeing</h1></div>:<></>
            }
            {
                checkedItems.oxigen_cylinder?<div><h1>oxigen_cylinder</h1></div>:<></>
            }
          </div>
          <h3>Trip Summary</h3>
          <RichTextEditor />
          {/* <h3>{}</h3>
          <ul>
            <li>
              Relish a perfect getaway to the land that offers the beauty of
              shimmering beaches, stellar nightlife, thrill of adventure
              activities and so much more
            </li>
            <li>
              Witness the architectural brilliance of Basilica of Bom Jesus, a
              UNESCO world heritage site that holds the mortal remains of St.
              Francis Xavier
            </li>
            <li>
              Walk barefoot on the soft sands under the clear sky and feel the
              mellow waves kissing your feet
            </li>
            <li>
              Treat your taste buds with delicious Goan cuisine and exotic Sea
              Food that also has a hint of Portuguese cuisine
            </li>
            <li>
              Marvel at the Goan shore line from the lighthouse of the famous
              Aguada Fort that proudly stands tall on the shores and offers
              panoramic views of the ocean
            </li>
          </ul>
          <h3>Backpacking Tour to Goa Overview</h3> */}
          {/* </div> */}
        </div>
        <div className="right-container">
          <div className="price-card">
            <p>Starting from INR 10,000</p>
            <h2>
              INR 7,299 <span>per Adult</span>
            </h2>
            <span>4D /3N</span>
          </div>
          <form onSubmit={handleSubmit}>
            <h3>Contact details</h3>
            <div className="form-group">
              <label htmlFor="name">Host Name</label>
              <input
                type="text"
                id="name"
                value={name}
                onChange={(e) => setName(e.target.value)}
              />
            </div>
            <div className="form-group">
              <label htmlFor="email">Host Email</label>
              <input
                type="email"
                id="email"
                value={email}
                onChange={(e) => setEmail(e.target.value)}
              />
            </div>
            <div className="form-group">
              <label htmlFor="phone">Host Phone number</label>
              <input
                type="tel"
                id="phone"
                value={phone}
                onChange={(e) => setPhone(e.target.value)}
              />
            </div>
            <div className="form-group">
              <label htmlFor="date">Host Website</label>
              <input
                type="link"
                id="websitelink"
                value={websitelink}
                onChange={(e) => setWebsitelink(e.target.value)}
              />
            </div>
            <div className="form-group">
              <label htmlFor="count">Host's Social Links</label>
              <input
                type="link"
                id="social"
                value={social}
                onChange={(e) => setSocial(e.target.value)}
              />
            </div>
            <div className="form-group">
              <label htmlFor="message">Message</label>
              <textarea
                id="message"
                value={message}
                onChange={(e) => setMessage(e.target.value)}
              />
            </div>
            <button type="submit">Book Now</button>
            <div className="privacy-notice">
            </div>
          </form>
        </div>
      </div>
    </div>
  );
}

export default Host_form;
