import React, { useState } from 'react';
import './iter_image_carousel_style.css';

const ImageCarousel = () => {
    const [currentIndex, setCurrentIndex] = useState(0);
    const images = [
      'https://via.placeholder.com/800x600?text=Image+1',
      'https://via.placeholder.com/800x600?text=Image+2',
      'https://via.placeholder.com/800x600?text=Image+3',
      'https://via.placeholder.com/800x600?text=Image+4',
      'https://via.placeholder.com/800x600?text=Image+5',
    ];
  
    const handleNext = () => {
      setCurrentIndex((currentIndex + 1) % images.length);
    };
  
    const handlePrev = () => {
      setCurrentIndex((currentIndex - 1 + images.length) % images.length);
    };
  
    const handleUpload = (index) => {
      console.log(`Upload button clicked on image ${index + 1}`);
      // Add your upload logic here
    };
  
    return (
      <div className="image-carousel">
        <div className="carousel-inner">
          {images.map((image, index) => (
            <div
              key={index}
              className={`carousel-item ${index === currentIndex? 'active' : ''}`}
            >
              <img src={image} alt={`Image ${index + 1}`} />
              <button
                className="upload-button"
                onClick={() => handleUpload(index)}
                style={{
                  position: 'absolute',
                  top: '50%',
                  left: '50%',
                  transform: 'translate(-50%, -50%)',
                }}
              >
                Upload
              </button>
            </div>
          ))}
        </div>
        <button className="carousel-prev" onClick={handlePrev}>
          &#10094;
        </button>
        <button className="carousel-next" onClick={handleNext}>
          &#10095;
        </button>
      </div>
    );
};

export default ImageCarousel;